import { Fragment, useState, useRef, useEffect } from "react";
import { useForm, Controller } from "react-hook-form";
import { useParams } from "react-router-dom";
import ToggleablePanel from "../../components/panels/ToogleablePanel";
import { InputNumber } from "primereact/inputnumber";
import { Dropdown } from "primereact/dropdown";
import { InputText } from "primereact/inputtext";
import { useReactToPrint } from "react-to-print";
import { classNames } from "primereact/utils";
import {
  createRepairForm,
  getRepairFormDetail,
  updateRepairForm,
} from "../../services/repair-service";
import {
  getCurrentDate,
  getExpiredDate,
  getDateWithFormat,
} from "../../utils/Utils";
import { Calendar } from "primereact/calendar";
import { Checkbox } from "primereact/checkbox";
import { InputTextarea } from "primereact/inputtextarea";
import Footer from "../../components/layout/footer/Footer";
import CarAutoComplete from "../../components/auto-complete/CarAutoComplete";
import MaintainanceCycleAutoComplete from "../../components/auto-complete/MaintainanceCycleAutoComplete";
import StatusDropdown from "../../components/dropdown/StatusDropdown";
import RepairTypeDropdown from "../../components/dropdown/RepairTypeDropdown";
import SparePartTable from "../../components/tables/SparePartTable";
import RepairFormPdf from "../repair-form/RepairFormPdf";

const defaultValues = {
  CarId: null,
  StatusId: 1,
  OrderCode: "",
  OrderDate: getCurrentDate(),
  DateIn: getCurrentDate(),
  DateOutEstimated: "",
  ODOCurrent: 0,
  ODONext: 5000,
  ODOUnit: "Km",
  ExpiredInDate: getExpiredDate(15),
  IsInvoice: true,
  AdvancePayment: 0,
  PaymentMethod: "CASH",
  Diagnosis: "",
  CustomerNote: "",
  InternalNote: "",
  Car: {
    LicensePlate: "",
  },
  OrderDetails: null,
};

const RepairFormDetailPage = () => {
  const formRef = useRef();
  const printComponentRef = useRef();
  const [selectedCar, setSelectedCar] = useState({});
  const [sparePartFromTemplate, setSparePartFromTemplate] = useState([]);
  const [advancePayment, setAdvancePayment] = useState(0);
  const [discountPercent, setDiscountPercent] = useState();
  const [printData, setPrintData] = useState(null);
  const [isProcessing, setIsProcessing] = useState({
    printing: false,
    saving: false,
  });
  const params = useParams();
  const [selectedRepairFormId, setSelectedRepairFormId] = useState(params?.id);

  const {
    control,
    formState: { errors, isDirty },
    handleSubmit,
    setValue,
    getValues,
    trigger,
    reset,
  } = useForm({ defaultValues });

  //get repairForm Detail
  useEffect(() => {
    if (selectedRepairFormId) {
      getRepairFormDetail(selectedRepairFormId).then((response) => {
        const data = response.data.Result;
        setSparePartFromTemplate(data.OrderDetails);
        setSelectedCar(data.Car);

        //convert date to IsoDateTime
        data.DateOutEstimated = new Date(data.DateOutEstimated);
        if (data.DateOutActual) {
          data.DateOutActual = new Date(data.DateOutActual);
        }
        reset(data);
      });
    }
  }, [reset, selectedRepairFormId]);

  const getFormErrorMessage = (name) => {
    return (
      errors[name] && <small className="p-error">{errors[name].message}</small>
    );
  };

  const updateNextODO = (value) => {
    if (value === null) {
      setValue("ODONext", 0);
      return;
    }
    const unit = getValues("ODOUnit");
    const currentODO = getValues("ODOCurrent");
    if (unit === "Km") {
      setValue("ODONext", currentODO + 5000);
    } else {
      setValue("ODONext", currentODO + 3100);
    }
  };

  const onMaintainanceCycleSelect = (maintainanceCycle) => {
    const sparePartList = maintainanceCycle.TemplateDetails;
    const existedSpareParts = getValues("OrderDetails");
    const updatedSparePartList = [...existedSpareParts, ...sparePartList];
    setSparePartFromTemplate(updatedSparePartList);
    setValue("TemplateId", maintainanceCycle.TemplateId);
  };

  const onHandleSparePartsChange = (spareParts) => {
    setValue("OrderDetails", spareParts, { shouldDirty: true });
  };

  const handlePrint = useReactToPrint({
    content: () => printComponentRef.current,
  });

  const functionButtons = [
    {
      label: "In Pdf",
      icon: "pi pi-print",
      className: "p-button-success",
      disabled: !selectedRepairFormId || isProcessing.printing,
      action: async () => {
        setIsProcessing({ ...isProcessing, printing: true });
        const res = await getRepairFormDetail(selectedRepairFormId);
        const data = res.data.Result;
        setPrintData(data);
        //wait for printData updated
        setTimeout(() => {
          handlePrint();
          setIsProcessing({ ...isProcessing, printing: false });
        }, 100);
      },
    },
    {
      label: "Lu",
      icon: "pi pi-check",
      className: "p-button-success",
      disabled: !isDirty || isProcessing.saving,
      action: async () => {
        const isFormValid = await trigger();
        if (isFormValid) {
          setIsProcessing({ ...isProcessing, saving: true });
          formRef.current.requestSubmit();
        }
      },
    },
  ];

  const onSubmit = (formValue) => {
    const { LicensePlate, DateOutEstimated, DateOutActual, ...data } =
      formValue;
    data.DateOutEstimated = getDateWithFormat(DateOutEstimated);
    if (DateOutActual) {
      data.DateOutActual = getDateWithFormat(DateOutActual);
    }
    if (data.OrderId) {
      updateRepairForm(data).finally(() => {
        setIsProcessing({ ...isProcessing, saving: false });
      });
      return;
    }
    createRepairForm(data)
      .then((res) => {
        const orderId = res.data.Result;
        setSelectedRepairFormId(orderId);
        setValue("OrderId", orderId);
      })
      .finally(() => {
        setIsProcessing({ ...isProcessing, saving: false });
      });
  };

  return (
    <Fragment>
      <div className="relative h-full pb-8">
        <form ref={formRef} onSubmit={handleSubmit(onSubmit)}>
          <ToggleablePanel header="Thng tin phiu" className="pb-2" toggleable>
            <div className="formgrid grid">
              <div className="field col-12 md:col-4">
                <label htmlFor="OrderCode">S phiu</label>
                <Controller
                  name="OrderCode"
                  control={control}
                  render={({ field }) => (
                    <InputText id={field.name} {...field} className="w-full" />
                  )}
                />
              </div>
              <div className="field col-12 md:col-4">
                <label htmlFor="OrderDate">Ngy lp phiu</label>
                <Controller
                  name="OrderDate"
                  control={control}
                  render={({ field, fieldState }) => (
                    <InputText
                      id={field.name}
                      {...field}
                      disabled
                      className={classNames("w-full", {
                        "p-invalid": fieldState.error,
                      })}
                    />
                  )}
                />
              </div>
              <div className="field col-12 md:col-4">
                <label htmlFor="StatusId">
                  Tnh trng phiu <b className="p-error">*</b>
                </label>
                <Controller
                  name="StatusId"
                  control={control}
                  rules={{ required: "Tnh trng khng c  trng!" }}
                  render={({ field, fieldState }) => (
                    <StatusDropdown field={field} fieldState={fieldState}/>
                  )}
                />
                {getFormErrorMessage("StatusName")}
              </div>
              <div className="field col-12 md:col-4">
                <label htmlFor="TypeId">
                  Loi phiu <b className="p-error">*</b>
                </label>
                <Controller
                  name="TypeId"
                  control={control}
                  rules={{ required: "Loi phiu khng c  trng!" }}
                  render={({ field, fieldState }) => (
                    <RepairTypeDropdown field={field} fieldState={fieldState}/>
                  )}
                />
                {getFormErrorMessage("TypeName")}
              </div>
              <div className="field col-12 md:col-4">
                <label htmlFor="ODOCurrent">
                  S ODO hin ti <b className="p-error">*</b>
                </label>

                <Controller
                  name="ODOCurrent"
                  control={control}
                  rules={{
                    required: "S ODO hin ti khng c  trng!",
                    min: 0,
                  }}
                  render={({ field, fieldState }) => (
                    <InputNumber
                      id={field.name}
                      ref={field.ref}
                      value={field.value}
                      onBlur={field.onBlur}
                      onValueChange={(e) => {
                        field.onChange(e);
                        updateNextODO(e.value);
                      }}
                      className={classNames("w-full", {
                        "p-invalid": fieldState.error,
                      })}
                    />
                  )}
                />
                {getFormErrorMessage("ODOCurrent")}
              </div>

              <div className="field col-12 md:col-4">
                <label htmlFor="ODOUnit">
                  n v ODO <b className="p-error">*</b>
                </label>
                <Controller
                  name="ODOUnit"
                  control={control}
                  rules={{ required: "n v ODO khng c  trng!" }}
                  render={({ field, fieldState }) => (
                    <Dropdown
                      id={field.name}
                      {...field}
                      options={["Km", "Miles"]}
                      placeholder="Chn n v ODO"
                      onSelect={(e) => updateNextODO(e.value)}
                      className={classNames("w-full", {
                        "p-invalid": fieldState.error,
                      })}
                    />
                  )}
                />
                {getFormErrorMessage("ODOUnit")}
              </div>

              <div className="field col-12 md:col-4">
                <label htmlFor="DateIn">Ngy vo</label>
                <Controller
                  name="DateIn"
                  control={control}
                  render={({ field, fieldState }) => (
                    <InputText
                      id={field.name}
                      {...field}
                      disabled
                      className={classNames("w-full", {
                        "p-invalid": fieldState.error,
                      })}
                    />
                  )}
                />
              </div>

              <div className="field col-12 md:col-4">
                <label htmlFor="ODONext">S ODO ln bo dng k tip</label>
                <Controller
                  name="ODONext"
                  control={control}
                  render={({ field, fieldState }) => (
                    <InputNumber
                      id={field.name}
                      {...field}
                      mode="decimal"
                      disabled
                      className={classNames("w-full", {
                        "p-invalid": fieldState.error,
                      })}
                    />
                  )}
                />
              </div>

              <div className="field col-12 md:col-4">
                <label htmlFor="DateOutEstimated">
                  Ngy d kin giao xe <b className="p-error">*</b>
                </label>
                <Controller
                  name="DateOutEstimated"
                  control={control}
                  rules={{
                    required: "Ngy d kin giao xe khng c  trng!",
                  }}
                  render={({ field, fieldState }) => (
                    <Calendar
                      id={field.name}
                      {...field}
                      showIcon
                      dateFormat="dd/mm/yy"
                      className={classNames("w-full", {
                        "p-invalid": fieldState.error,
                      })}
                    />
                  )}
                />
                {getFormErrorMessage("DateOutEstimated")}
              </div>

              <div className="field col-12 md:col-4">
                <label htmlFor="ExpiredInDate">Thi hn bo gi</label>
                <Controller
                  name="ExpiredInDate"
                  control={control}
                  render={({ field, fieldState }) => (
                    <InputText
                      id={field.name}
                      {...field}
                      disabled
                      className={classNames("w-full", {
                        "p-invalid": fieldState.error,
                      })}
                    />
                  )}
                />
              </div>

              <div className="field col-12 md:col-4">
                <label htmlFor="AdvancePayment">Tm ng</label>
                <Controller
                  name="AdvancePayment"
                  control={control}
                  render={({ field }) => (
                    <InputNumber
                      id={field.name}
                      ref={field.ref}
                      value={field.value}
                      onBlur={field.onBlur}
                      onValueChange={(e) => {
                        field.onChange(e);
                        setAdvancePayment(e.value);
                      }}
                      mode="currency"
                      currency="VND"
                      currencyDisplay="code"
                      locale="vi-VN"
                      className="w-full"
                    />
                  )}
                />
              </div>

              <div className="field col-12 md:col-4">
                <label htmlFor="Discount">Chit khu</label>
                <Controller
                  name="Discount"
                  control={control}
                  render={({ field }) => (
                    <InputNumber
                      id={field.name}
                      ref={field.ref}
                      value={field.value}
                      onBlur={field.onBlur}
                      onValueChange={(e) => {
                        field.onChange(e);
                        setDiscountPercent(e.value);
                      }}
                      suffix=" %"
                      mode="decimal" min={0} max={100}
                      className="w-full"
                    />
                  )}
                />
              </div>

              <div className="field col-12 md:col-4">
                <label htmlFor="DateOutActual">Ngy giao xe thc t</label>
                <Controller
                  name="DateOutActual"
                  control={control}
                  render={({ field, fieldState }) => (
                    <Calendar
                      id={field.name}
                      {...field}
                      showIcon
                      dateFormat="dd/mm/yy"
                      className={classNames("w-full", {
                        "p-invalid": fieldState.error,
                      })}
                    />
                  )}
                />
              </div>

              <div className="field col-12 md:col-4">
                <label htmlFor="PaymentMethod">
                  Hnh thanh ton <b className="p-error">*</b>
                </label>
                <Controller
                  name="PaymentMethod"
                  control={control}
                  rules={{ required: "Hnh thanh ton khng c  trng!" }}
                  render={({ field, fieldState }) => (
                    <Dropdown
                      id={field.name}
                      {...field}
                      optionLabel="label"
                      optionValue="id"
                      options={[
                        { id: "CASH", label: "Tin mt" },
                        { id: "TRANSFER", label: "Chuyn khon" },
                      ]}
                      placeholder="Chn Hnh thanh ton"
                      onSelect={(e) => updateNextODO(e.value)}
                      className={classNames("w-full", {
                        "p-invalid": fieldState.error,
                      })}
                    />
                  )}
                />
                {getFormErrorMessage("PaymentMethod")}
              </div>

              <div className="field col-12 md:col-4">
                <label htmlFor="IsInvoice">Xut ha n GTGT </label>
                <Controller
                  name="IsInvoice"
                  control={control}
                  render={({ field }) => (
                    <div className="w-full py-2">
                      <Checkbox
                        inputId={field.name}
                        onChange={(e) => field.onChange(e.checked)}
                        checked={field.value}
                      />
                    </div>
                  )}
                />
              </div>

              <div className="field col-12 md:col-4">
                <label htmlFor="Diagnosis">
                  Chun on/tnh trng khi vo xng{" "}
                  <b className="p-error">*</b>
                </label>
                <Controller
                  name="Diagnosis"
                  control={control}
                  rules={{ required: "Chun on khng c  trng!" }}
                  render={({ field, fieldState }) => (
                    <InputTextarea
                      id={field.name}
                      {...field}
                      rows={5}
                      className={classNames("w-full", {
                        "p-invalid": fieldState.error,
                      })}
                    />
                  )}
                />
                {getFormErrorMessage("Diagnosis")}
              </div>

              <div className="field col-12 md:col-4">
                <label htmlFor="CustomerNote">Ghi ch khch hng</label>
                <Controller
                  name="CustomerNote"
                  control={control}
                  render={({ field }) => (
                    <InputTextarea
                      id={field.name}
                      {...field}
                      rows={5}
                      className="w-full"
                    />
                  )}
                />
              </div>

              <div className="field col-12 md:col-4">
                <label htmlFor="InternalNote">Ghi ch ni b</label>
                <Controller
                  name="InternalNote"
                  control={control}
                  render={({ field }) => (
                    <InputTextarea
                      id={field.name}
                      rows={5}
                      {...field}
                      className="w-full"
                    />
                  )}
                />
              </div>
            </div>
          </ToggleablePanel>
          <ToggleablePanel
            header="Thng tin xe v khch hng"
            className="pb-2"
            toggleable
          >
            <div className="formgrid grid">
              <div className="field col-12 md:col-4">
                <label htmlFor="LicensePlate">
                  Bin s xe <b className="p-error">*</b>
                </label>
                <Controller
                  name="Car.LicensePlate"
                  control={control}
                  rules={{ required: "Bin s xe khng c  trng!" }}
                  render={({ field, fieldState }) => (
                    <CarAutoComplete
                      field={field}
                      fieldState={fieldState}
                      setValue={setValue}
                      onSelectCar={setSelectedCar}
                    />
                  )}
                />
                {getFormErrorMessage("LicensePlate")}
              </div>

              <div className="field col-12 md:col-4">
                <label htmlFor="MaintainCycle">Chu k bo dng</label>
                <MaintainanceCycleAutoComplete
                  id="MaintainCycle"
                  onSelect={onMaintainanceCycleSelect}
                />
              </div>

              <div className="field col-12 md:col-4">
                <label htmlFor="ManufacturerName">Hng xe</label>
                <InputText
                  id="ManufacturerName"
                  value={selectedCar.ManufacturerName || ""}
                  disabled
                  className="w-full"
                />
              </div>
              <div className="field col-12 md:col-4">
                <label htmlFor="TypeName">Dng xe</label>
                <InputText
                  id="TypeName"
                  value={selectedCar.TypeName || ""}
                  disabled
                  className="w-full"
                />
              </div>

              <div className="field col-12 md:col-4">
                <label htmlFor="YearOfManufacture">Nm sn xut</label>
                <InputText
                  id="YearOfManufacture"
                  value={selectedCar.YearOfManufacture || ""}
                  disabled
                  className="w-full"
                />
              </div>

              <div className="field col-12 md:col-4">
                <label htmlFor="VIN">VIN</label>
                <InputText
                  id="VIN"
                  value={selectedCar.VIN || ""}
                  disabled
                  className="w-full"
                />
              </div>

              <div className="field col-12 md:col-4">
                <label htmlFor="PhoneNumber">S in thoi</label>
                <InputText
                  id="PhoneNumber"
                  value={selectedCar.Customer?.PhoneNumber || ""}
                  disabled
                  className="w-full"
                />
              </div>

              <div className="field col-12 md:col-4">
                <label htmlFor="FullName">Khch hng</label>
                <InputText
                  id="FullName"
                  value={selectedCar.Customer?.FullName || ""}
                  disabled
                  className="w-full"
                />
              </div>

              <div className="field col-12 md:col-4">
                <label htmlFor="Email">Email</label>
                <InputText
                  id="Email"
                  value={selectedCar.Customer?.Email || ""}
                  disabled
                  className="w-full"
                />
              </div>

              <div className="field col-12 md:col-4">
                <label htmlFor="Address">a ch</label>
                <InputText
                  id="Address"
                  value={selectedCar.Customer?.Address || ""}
                  disabled
                  className="w-full"
                />
              </div>

              <div className="field col-12 md:col-4">
                <label htmlFor="CustomerTypeName">Loi khch hng</label>
                <InputText
                  id="CustomerTypeName"
                  value={selectedCar.Customer?.TypeName || ""}
                  disabled
                  className="w-full"
                />
              </div>

              <div className="field col-12 md:col-4">
                <label htmlFor="CustomerTaxCode">M s thu</label>
                <InputText
                  id="CustomerTaxCode"
                  value={selectedCar.Customer?.TaxCode || ""}
                  disabled
                  className="w-full"
                />
              </div>
            </div>
          </ToggleablePanel>
        </form>
        <ToggleablePanel
          header="Chi ph ph tng sa cha"
          className="pb-2 mb-8"
          toggleable
        >
          <SparePartTable
            existedSpareParts={sparePartFromTemplate}
            handleSparePartsChange={onHandleSparePartsChange}
            advancePayment={advancePayment}
            discountPercent={discountPercent}
            isRepairForm={true}
          />
        </ToggleablePanel>
        <Footer items={functionButtons} />
      </div>
      <div style={{ display: "none" }}>
        <RepairFormPdf ref={printComponentRef} data={printData} />
      </div>
    </Fragment>
  );
};

export default RepairFormDetailPage;
