import { Fragment, useState } from "react";
import { Fieldset } from "primereact/fieldset";
import { Calendar } from "primereact/calendar";
import { Button } from "primereact/button";
import { Link } from "react-router-dom";
import { classNames } from "primereact/utils";
import { useForm, Controller } from "react-hook-form";
import CarAutoComplete from "../../components/auto-complete/CarAutoComplete";
import CustomerAutoComplete from "../../components/auto-complete/CustomerAutoComplete";
import StatusDropdown from "../../components/dropdown/StatusDropdown";
import AppDataTable from "../../components/tables/AppDataTable";
import classes from "../repair-form/RepairForm.module.scss";
import {
  getRepairForms,
  getRepairFormsExport,
} from "../../services/repair-service";
import { getDateWithFormat } from "../../utils/Utils";

const defaultValues = {
  customerId: null,
  carId: null,
  createdFromDate: undefined,
  createdToDate: undefined,
  statusId: null,
  typeId: null,
  dateInFrom: undefined,
  dateInTo: undefined,
  dateOutFrom: undefined,
  dateOutTo: undefined,
};

const ReportPage = () => {
  const [repairForms, setRepairForms] = useState([]);
  const [paginatorOptions, setPaginatorOptions] = useState();

  const {
    control,
    formState: { errors, isDirty, isValid },
    handleSubmit,
    setValue,
    getValues,
    reset,
  } = useForm({ defaultValues });

  const getFormErrorMessage = (name) => {
    return (
      errors[name] && <small className="p-error">{errors[name].message}</small>
    );
  };

  const validateFromDateToDate = (
    value,
    compareField,
    isFromDate,
    fieldLabel
  ) => {
    const compareDate = getValues()[compareField];
    if (!value && !compareDate) {
      return true;
    }

    if (!value && compareDate) {
      return `${fieldLabel} khng th  trng`;
    }

    if (isFromDate && value > compareDate) {
      return `${fieldLabel} t khng th ln hn ${fieldLabel} n!`;
    }

    if (!isFromDate && value < compareDate) {
      return `${fieldLabel} n khng th nh hn ${fieldLabel} t!`;
    }
  };

  const statusBodyTemplate = (rowData) => {
    return (
      <span
        className={classNames(
          classes.status_badge,
          classes[`status_${rowData.StatusId}`]
        )}
      >
        {rowData.StatusName}
      </span>
    );
  };

  const columns = [
    {
      field: "OrderId",
      header: "M phiu",
      body: (rowData) => {
        return (
          <Link to={`/app/repair-detail/${rowData.OrderId}`}>
            {rowData.OrderId}
          </Link>
        );
      },
    },
    {
      field: "Car.LicensePlate",
      header: "Bin s",
    },
    {
      field: "Car.ManufacturerName",
      header: "Hng xe",
    },
    {
      field: "Car.TypeName",
      header: "Dng xe",
    },
    {
      field: "Car.Customer.FullName",
      header: "Tn khch hng",
      body: (rowData) => {
        return (
          <Link to={`/app/customer-detail/${rowData.Car.Customer.CustomerId}`}>
            {rowData.Car.Customer.FullName}
          </Link>
        );
      },
    },
    {
      field: "StatusName",
      header: "Trng thi",
      body: statusBodyTemplate,
    },
    {
      field: "OrderDate",
      header: "Ngy lp phiu",
    },
  ];

  const exportReport = (keyword) => {
    const { FullName, LicensePlate, ...searchParameters } = getValues();
    return getRepairFormsExport(keyword, searchParameters);
  };

  const getData = (pageSize, pageIndex, keyword) => {
    const { FullName, LicensePlate, ...searchParameters } = getValues();
    getRepairForms(pageSize, pageIndex, keyword, searchParameters).then(
      (response) => {
        const { Data, ...paginatorOptions } = response.data.Result;
        setPaginatorOptions(paginatorOptions);
        setRepairForms(Data);
      }
    );
  };

  const onSubmit = (data) => {
    const { pageSize, pageIndex } = paginatorOptions;
    const { FullName, LicensePlate, ...searchParameters } = data;
    getRepairForms(pageSize, pageIndex, "", searchParameters).then(
      (response) => {
        if (!response.data.IsSuccess) {
          return;
        }
        const { Data, ...paginatorOptions } = response.data.Result;
        setPaginatorOptions(paginatorOptions);
        setRepairForms(Data);
      }
    );
  };
  return (
    <Fragment>
      <Fieldset className="mb-4" legend="Thng tin tm kim" toggleable>
        <form onSubmit={handleSubmit(onSubmit)}>
          <div className="formgrid grid">
            <div className="field col-12 md:col-4">
              <label htmlFor="LicensePlate">Bin s xe</label>
              <Controller
                name="LicensePlate"
                control={control}
                render={({ field, fieldState }) => (
                  <CarAutoComplete
                    field={field}
                    fieldState={fieldState}
                    setValue={setValue}
                  />
                )}
              />
            </div>

            <div className="field col-12 md:col-4">
              <label htmlFor="FullName">Khch hng</label>
              <Controller
                name="FullName"
                control={control}
                render={({ field, fieldState }) => (
                  <CustomerAutoComplete
                    field={field}
                    fieldState={fieldState}
                    setValue={setValue}
                  />
                )}
              />
            </div>

            <div className="field col-12 md:col-4">
              <label htmlFor="StatusId">Tnh trng phiu</label>
              <Controller
                name="StatusId"
                control={control}
                render={({ field, fieldState }) => (
                  <StatusDropdown field={field} fieldState={fieldState} />
                )}
              />
            </div>

            <div className="field col-12 md:col-4">
              <label htmlFor="createdFromDate">Ngy lp phiu t</label>
              <Controller
                name="createdFromDate"
                control={control}
                rules={{
                  validate: {
                    isDateValid: (value) =>
                      validateFromDateToDate(
                        value,
                        "createdToDate",
                        true,
                        "Ngy lp phiu"
                      ),
                  },
                }}
                render={({ field, fieldState }) => (
                  <Calendar
                    value={field.value}
                    onChange={(date) =>
                      field.onChange(getDateWithFormat(date.value))
                    }
                    showIcon
                    autoFocus
                    dateFormat="dd/mm/yy"
                    className={classNames("w-full", {
                      "p-invalid": fieldState.error,
                    })}
                  />
                )}
              />
              {getFormErrorMessage("createdFromDate")}
            </div>

            <div className="field col-12 md:col-4">
              <label htmlFor="createdToDate">Ngy lp phiu n</label>
              <Controller
                name="createdToDate"
                control={control}
                rules={{
                  validate: {
                    isDateValue: (value) =>
                      validateFromDateToDate(
                        value,
                        "createdFromDate",
                        false,
                        "Ngy lp phiu"
                      ),
                  },
                }}
                render={({ field, fieldState }) => (
                  <Calendar
                    value={field.value}
                    onChange={(date) =>
                      field.onChange(getDateWithFormat(date.value))
                    }
                    showIcon
                    dateFormat="dd/mm/yy"
                    className={classNames("w-full", {
                      "p-invalid": fieldState.error,
                    })}
                  />
                )}
              />
              {getFormErrorMessage("createdToDate")}
            </div>

            <div className="col-12 md:col-4"></div>

            <div className="field col-12 md:col-4">
              <label htmlFor="dateInFrom">Ngy vo t</label>
              <Controller
                name="dateInFrom"
                control={control}
                rules={{
                  validate: {
                    isDateValue: (value) =>
                      validateFromDateToDate(
                        value,
                        "dateInTo",
                        true,
                        "Ngy vo"
                      ),
                  },
                }}
                render={({ field, fieldState }) => (
                  <Calendar
                    value={field.value}
                    onChange={(date) =>
                      field.onChange(getDateWithFormat(date.value))
                    }
                    showIcon
                    autoFocus
                    dateFormat="dd/mm/yy"
                    className={classNames("w-full", {
                      "p-invalid": fieldState.error,
                    })}
                  />
                )}
              />
              {getFormErrorMessage("dateInFrom")}
            </div>

            <div className="field col-12 md:col-4">
              <label htmlFor="dateInTo">Ngy vo n</label>
              <Controller
                name="dateInTo"
                control={control}
                rules={{
                  validate: {
                    isDateValue: (value) =>
                      validateFromDateToDate(
                        value,
                        "dateInFrom",
                        false,
                        "Ngy vo"
                      ),
                  },
                }}
                render={({ field, fieldState }) => (
                  <Calendar
                    value={field.value}
                    onChange={(date) =>
                      field.onChange(getDateWithFormat(date.value))
                    }
                    showIcon
                    dateFormat="dd/mm/yy"
                    className={classNames("w-full", {
                      "p-invalid": fieldState.error,
                    })}
                  />
                )}
              />
              {getFormErrorMessage("dateInTo")}
            </div>

            <div className="col-12 md:col-4"></div>

            <div className="field col-12 md:col-4">
              <label htmlFor="dateOutFrom">Ngy ra t</label>
              <Controller
                name="dateOutFrom"
                control={control}
                rules={{
                  validate: {
                    isDateValue: (value) =>
                      validateFromDateToDate(
                        value,
                        "dateOutTo",
                        true,
                        "Ngy ra"
                      ),
                  },
                }}
                render={({ field, fieldState }) => (
                  <Calendar
                    value={field.value}
                    onChange={(date) =>
                      field.onChange(getDateWithFormat(date.value))
                    }
                    showIcon
                    autoFocus
                    dateFormat="dd/mm/yy"
                    className={classNames("w-full", {
                      "p-invalid": fieldState.error,
                    })}
                  />
                )}
              />
              {getFormErrorMessage("dateOutFrom")}
            </div>

            <div className="field col-12 md:col-4">
              <label htmlFor="dateOutTo">Ngy ra n</label>
              <Controller
                name="dateOutTo"
                control={control}
                rules={{
                  validate: {
                    isDateValue: (value) =>
                      validateFromDateToDate(
                        value,
                        "dateOutFrom",
                        false,
                        "Ngy ra"
                      ),
                  },
                }}
                render={({ field, fieldState }) => (
                  <Calendar
                    value={field.value}
                    onChange={(date) =>
                      field.onChange(getDateWithFormat(date.value))
                    }
                    showIcon
                    dateFormat="dd/mm/yy"
                    className={classNames("w-full", {
                      "p-invalid": fieldState.error,
                    })}
                  />
                )}
              />
              {getFormErrorMessage("dateOutTo")}
            </div>
          </div>

          <div className="text-right">
            <Button
              disabled={!isDirty}
              type="button"
              label="Xo"
              icon="pi pi-refresh"
              className="mt-2 mr-4"
              onClick={() => reset(defaultValues)}
            />
            <Button
              disabled={!isValid || !isDirty}
              type="submit"
              label="Tm kim"
              icon="pi pi-search"
              className="mt-2"
            />
          </div>
        </form>
      </Fieldset>

      <AppDataTable
        data={repairForms}
        columns={columns}
        dataKey="TemplateId"
        title="Phiu bo dng / sa cha"
        isHideBodyActions={true}
        isHideCreateButton={true}
        excelExportable={true}
        excelFileName="Bo co"
        paginatorOptions={paginatorOptions}
        fnGetData={getData}
        fnGetAllDataForExport={exportReport}
      />
    </Fragment>
  );
};

export default ReportPage;
